@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap');
* {
    box-sizing: border-box;
    margin: 0;
}

body{
    font-family: 'Lato', sans-serif;
}

header {
    background-color: #347FC4;
    padding: 20px;
    text-align: center;
}

header h1 {
    text-transform: uppercase;
}

button{
    background: none;
    border: none;
    outline: 1px solid black;
}

.flex {
    display: flex;
    align-items: center;
}

.info-box, .search-box {
    margin-top: 50px;
    text-align: center;
    margin-left: 50px;
    margin-right: 50px;
    flex: 1;
}

.search-box button{
    margin: 5px;
    padding: 10px;
    font-size: 1em;
}




.flex-nocenter{
    display: flex;
    justify-content: space-between;
}

.search-box h2{
    background-color: #347FC4;
    padding: 5px;
    text-transform: uppercase;
    font-size: 1.3em;
    letter-spacing: 2px;
}

.search-box input{
    padding: 10px;
    font-size: 1em;
    width: 300px;
}


.flex.name{
    justify-content: space-between;
}

.name button {
    margin-left: 100px;
}
.name{
    margin-bottom: 5px;
}




.container{
    margin: 0 auto;
    max-width: 800px;
    width: 100%;

}

.relative{
    position: relative;
}

.amount {
    margin-bottom: 20px;
}


.total {
    text-align: left;
    background-color: #347FC4;
    padding: 10px;
    font-size: 1.2em;
    width: 100%;
    border-radius: 10px;
    margin-bottom: 150px;
}
